import * as React from "react";
import { Button, Col, Row, Stack, Container } from "react-bootstrap";
import { Player } from "../Player";
import InputForm from "./InputForm";

import AnswerForm from "./AnswerForm";

import { Notes } from "./Notes";
import { Utils } from "../Utils";
import { Config } from "../Config";

interface IIntervalTrainer {}

const IntervalTrainer: React.FC<IIntervalTrainer> = () => {
  const [generatedNotes, setGeneratedNotes] = React.useState<string[]>([]);
  const [rootNote, setRootNote] = React.useState(Notes.getNotes()[0]);
  const [interval, setInterval] = React.useState(Notes.intervals[0][0]);
  const [numberOfNotes, setNumberOfNotes] = React.useState(
    "" + Config.initialNumberOfNotes
  );

  const generateNotes = () => {
    Utils.debugLog(
      `Generating for rootNode: ${rootNote} interval: ${interval}, numberOfNotes: ${numberOfNotes}`
    );
    const notes = Notes.generateNotes(
      rootNote,
      interval,
      parseInt(numberOfNotes)
    );
    setGeneratedNotes(notes);
    Utils.debugLog("Notes: ");
    Utils.debugLog(notes);
    Player.playNotes(notes);
  };

  const playRootNote = () => {
    Player.playNotes([rootNote]);
  };

  return (
    <Container>
      <Row>
        <Col>
          <InputForm
            setRootNote={setRootNote}
            setInterval={setInterval}
            setNumberOfNotes={setNumberOfNotes}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Stack direction="horizontal" gap={3}>
            <Button className="mt-3" onClick={playRootNote}>
              Play Root Note
            </Button>
            <Button className="mt-3" onClick={generateNotes}>
              Generate Notes
            </Button>
          </Stack>
        </Col>
      </Row>

      <Row>
        <Col>
          <AnswerForm notes={generatedNotes} />
        </Col>
      </Row>
    </Container>
  );
};

export default IntervalTrainer;
