import * as React from "react";
import { Button, Col, Form, Row, Stack, Table, Alert } from "react-bootstrap";
import { Player } from "../Player";
import { NotesAligner} from "./NotesAligner"

interface IAnswerFormProps {
  notes: string[];
}

const AnswerForm: React.FC<IAnswerFormProps> = ({ notes }) => {
  const [playedNotes, setPlayedNotes] = React.useState<string[]>([]);
  const [answerText, setAnswerText] = React.useState("");
  const [answerNotes, setAnswerNotes] = React.useState<string[]>([]);
  const [score, setScore] = React.useState("");
  const [useAlign, setUseAlign] = React.useState(true);

  const checkAnswer = () => {
    setPlayedNotes(notes);
    updateScore();
  };
  React.useEffect(() => {
    // Runs if useAlign changes
    if(playedNotes.length===0){
      return;
    }
    updateScore();
    // eslint-disable-next-line 
  },[useAlign]);

  function updateScore() {
    let userNotes = answerText
      .split(/\s+/)
      .map((x) => x.toUpperCase())
      .slice(0, notes.length);
    
    if(useAlign){
      userNotes = NotesAligner.alignNotes(notes,userNotes);
    }  
    setAnswerNotes(userNotes);

    const correct = userNotes.filter((val, index) => val === notes[index].replace(/\d/,'')).length;
    const percent = ((correct * 100.0) / notes.length).toFixed(2);
    const scoreText = `Score: ${correct}/${notes.length} (${percent}%)`;
    setScore(scoreText);
  }

  return (
    <Form className="mt-3">
      <div className="container-fluid" style={{ border: "2px solid #cecece" }}>
        <Row className="mb-3">
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Enter the notes you heard separated by spaces</Form.Label>
            <Form.Control type="email" placeholder="Enter notes" onChange={(event) => setAnswerText(event.target.value)} />
            <Form.Text className="text-muted">For example: C# D E F#</Form.Text>
          </Form.Group>
        </Row>

        <Row className="mb-3">
          <Col>
            <Stack direction="horizontal" gap={3}>
              <Button className="mt-3" onClick={(e) => Player.playNotes(notes)} disabled={notes.length === 0}>
                Play Again
              </Button>
              <Button className="mt-3" onClick={checkAnswer} disabled={notes.length === 0}>
                Check Answer
              </Button>
            </Stack>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Form.Check type="checkbox" label="Align notes when scoring" checked={useAlign} onChange={e=>setUseAlign(e.target.checked)} />
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Table responsive>
              <thead>
                <tr>
                  <th>Actual</th>
                  {Array.from({ length: playedNotes.length }).map((_, index) => (
                    <th key={index}>{playedNotes[index]}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Your Answer</td>
                  {Array.from({ length: answerNotes.length }).map((_, index) => (
                    <td
                      key={index}
                      style={{
                        backgroundColor: answerNotes[index] === playedNotes[index] ? "inherit" : "pink",
                      }}
                    >
                      {answerNotes[index]}
                    </td>
                  ))}
                </tr>
              </tbody>
            </Table>
          </Col>
        </Row>

        <Row className="mb-3">
          <Col>
            <Alert variant="success">
              <Alert.Heading>{score}</Alert.Heading>
            </Alert>
          </Col>
        </Row>
      </div>
    </Form>
  );
};

export default AnswerForm;
