import * as React from "react";
import { Form, Container, Row, Col } from "react-bootstrap";
import {Utils} from "../Utils"
interface IFretboardMasterProps {}


const FretboardMaster: React.FC<IFretboardMasterProps> = () => {

  const naturalNotes = ["C", "D", "E", "F", "G", "A", "B"];
  const chromaticNotes = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];

  const [isNatural, setIsNatural] = React.useState(true);
  const [isFiveSecond, setIsFiveSecond] = React.useState(true);
  const [displayedNote, setDisplayedNote] = React.useState(getNextDisplayString());
  const intervalIDRef = React.useRef<NodeJS.Timeout>();

  const setChromatic = (val: string) => {
    setIsNatural(val==="natural")
  };

  const setSpeed = (val: string) => {
    setIsFiveSecond(val === "5");
  };

  function getNextDisplayString(){
    return Utils.randomInteger(1, 7)+' '+getRandomNote()
  }

  

  function getRandomNote() {
    return isNatural ? getRandomNaturalNote() : getRandomChromaticNote();
  }
  function getRandomNaturalNote() {
    return naturalNotes[Utils.randomInteger(0, naturalNotes.length)];
  }
  function getRandomChromaticNote() {
    return chromaticNotes[Utils.randomInteger(0, chromaticNotes.length)];
  }

  React.useEffect(() => {
    if (intervalIDRef.current) {
      clearInterval(intervalIDRef.current);
    }
    intervalIDRef.current = setInterval(() => {
      setDisplayedNote(getNextDisplayString());
    }, 1000 * (isFiveSecond ? 5 : 10));
    // eslint-disable-next-line
  }, [isFiveSecond,isNatural]);

  return (
    <Container>
      <Form className="mt-3">
        <Row className="mb-3">
          <Col>
            <div onChange={(e) => setChromatic((e.target as HTMLInputElement).value)}>
              <Form.Check inline label="Chromatic" name="isNatural" type="radio" id="chromatic" value="chromatic" />
              <Form.Check inline label="Natural" name="isNatural" type="radio" id="natural" value="natural" defaultChecked={true} />
            </div>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col>
            <div onChange={(e) => setSpeed((e.target as HTMLInputElement).value)}>
              <Form.Check inline label="5 seconds" name="speed" type="radio" id="5sec" value="5" defaultChecked={true} />
              <Form.Check inline label="10 seconds" name="speed" type="radio" id="10sec" value="10" />
            </div>
          </Col>
        </Row>

        <Row className="mb-3">
          <div style={{ color: "black", fontSize: "150px", textAlign: "center", padding: "6px" }}> 
            {displayedNote}
          </div>
        </Row>
      </Form>
    </Container>
  );
};

export default FretboardMaster;
