import * as React from "react";
import { Col, Form, Row } from "react-bootstrap";
import { Notes } from "./Notes";
import {Config} from "../Config";

interface IInputFormProps {
  setRootNote: React.Dispatch<React.SetStateAction<string>>;
  setInterval: React.Dispatch<React.SetStateAction<string>>;
  setNumberOfNotes: React.Dispatch<React.SetStateAction<string>>;
}

const InputForm: React.FC<IInputFormProps> = ({
  setRootNote,
  setInterval,
  setNumberOfNotes,
}) => {
  return (
    <Form className="mt-3">
      <div className="container-fluid" style={{ border: "2px solid #cecece" }}>
        <Row className="mb-3">
          <Col xs={6} md={4}>
            <Form.Group controlId="formRootNote">
              <Form.Label>Root Note</Form.Label>
              <Form.Select
                onChange={(event) =>
                  setRootNote((event.target as HTMLInputElement).value)
                }
              >
                {Notes.getNotes().map((val) => (
                  <option key={val}>{val}</option>
                ))}
              </Form.Select>
            </Form.Group>
          </Col>
          <Col xs={6} md={4}>
            <Form.Group controlId="formInterval">
              <Form.Label>Highest Interval</Form.Label>
              <Form.Select
                onChange={(event) =>
                  setInterval((event.target as HTMLInputElement).value)
                }
              >
                {Notes.intervals.map((val) => (
                  <option key={val[0]}>{val[0]}</option>
                ))}
              </Form.Select>
            </Form.Group>
          </Col>

          {/* Have this column take up the whole width on mobile */}
          <Col xs={12} md={4}>
            <Form.Group controlId="formNumberOfNotes">
              <Form.Label>Number of Notes</Form.Label>
              <Form.Control
                type="number"
                defaultValue={Config.initialNumberOfNotes}
                onChange={(event) => setNumberOfNotes(event.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>
      </div>
    </Form>
  );
};

export default InputForm;
